import { useState, useEffect } from 'react';
import {
    Button,
    Switch,
    InputLabel,
    MenuItem,
    FormControl,
    Select,
    TextField,
    autocompleteClasses,
    Box,
    Container,
    Stack
} from "@mui/material"
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormLabel from '@mui/material/FormLabel';
import axios from 'axios';
import { useForm } from "react-hook-form";
import Typography from '@mui/material/Typography';
import { useSettingsContext } from '../../components/settings/SettingsContext';
import { variables } from "../../variables";
import Position from "./Positions"
import OfficeLocation from "./OfficeLocation"
import Nationality from './Nationality';
import Calendar from './Calendar';
import Gender from "./Gender"
import MaritalStatus from "./MaritalStatus"
import Notification from '../../components/Notifications';


function EditProfile(props) {
    const { recordForEdit, inserdata, setOpenPopup } = props

    // const recordForEdit = {
    //     "employeeID": 1007,
    //     "fullName": "Cilmi",
    //     "hireDate": "2022-12-27T07:38:11.017",
    //     "possitionId": 1,
    //     "isManager": false,
    //     "officelocation": 1
    // }

    // console.log(recordForEdit)
    const { register, handleSubmit, formState: { errors } } = useForm({
        mode: "onBlur"
    });
    // const [openPopup, setOpenPopup] = useState(false)

    const { themeStretch } = useSettingsContext();

    const [Titles, setTitles] = useState([]);
    const getTitles = async () => {
        try {
            const Endpoint = `Titles`
            const data = await axios.get(variables.API_URL + Endpoint);

            setTitles(data.data)

        }
        catch (e) {
            console.log(e);
        }
    }

    useEffect(() => {
        getTitles();
    }, []);

    const handleCancel = () => {
        setOpenPopup(false)
    }

    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })

    const registerOptions = {
        employeeID: { required: "Employee" },
        name: { required: "Name is required" },
        email: { required: "Email is required" },
        password: {
            required: "Password is required",
            minLength: {
                value: 8,
                message: "Password must have at least 8 characters"
            }
        },
        
        // email: { required: "Email is required" },
        // email: { required: "Email is required" },
        // email: { required: "Email is required" },
        // email: { required: "Email is required" },
    };
    function handleRegistration(values) {
        // console.log("diam", event)
        // const json = JSON.stringify(event)
        // console.log(values)
        // console.log("Hi")
        inserdata(values)
        setOpenPopup(false)
    }
    function handleError(event) { }
    const label = { inputProps: { 'aria-label': 'Switch demo' } };

    return (
        <Container maxWidth="sm" sx={{ margin: 0 }}>
            <Box
                sx={{ padding: '10px', borderRadius: '10px' }}
            >
                <form onSubmit={handleSubmit(handleRegistration, handleError)}>
                    <Box>
                        <Typography variant="h5" display="h5">{recordForEdit.fullName}</Typography>
                    </Box>
                    <br />
                    <Stack direction="row" spacing={2}>
                        <Box flex={2}>
                            <TextField
                                id="employeeID"
                                label="Employee Id"
                                sx={{ width: '238px' }}
                                name="employeeID"
                                // defaultValue={recordForEdit.employeeID}
                                value={recordForEdit.employeeID}
                                {...register('employeeID', {required: true})}
                            />
                        </Box>
                        <Box flex={2}>
                            <TextField
                                id="Cellphone"
                                label="Cell phone *"
                                type={'number'}
                                sx={{ width: '238px' }}
                                name="Cellphone"
                                defaultValue={recordForEdit.Cellphone}
                                {...register('Cellphone', {required: true})}
                            />
                            <Typography sx={{color:'red'}}>{errors.Cellphone?.type === "required" && "Cell phone is required"}</Typography>
                        </Box>
                    </Stack>
                    <br />
                    <Stack direction="row" spacing={2}>
                    <Box flex={2}>
                            <TextField
                                id="Line"
                                label="Line"
                                sx={{ width: '238px' }}
                                type={'number'}
                                name="Line"
                                {...register('Line')}
                            />
                        </Box>
                        <Box flex={2}>
                            <TextField
                                id="Email"
                                label="Email *"
                                sx={{ width: '238px' }}
                                type={'email'}
                                name="Email"
                                defaultValue={recordForEdit.Email}
                                {...register('Email', {required: true})}
                            />
                            <Typography sx={{color:'red'}}>{errors.Email?.type === "required" && "Email is required"}</Typography>
                        </Box>
                      
                    </Stack>
                    <br />
                    <stack direction="row" spacing={2}>
                    <Box flex={3}>
                            <Nationality register={register} errors={errors} />
                        </Box>
                        <br />
                        <Box flex={3} >
                            <Calendar register={register} errors={errors} />
                        </Box>
                    </stack>
                    <br />
                    <Stack direction="row" spacing={2}>
                        <Box flex={2}>
                            <TextField
                                id="payrollCurrenty"
                                label="Payroll Currency *"
                                name="payrollCurrenty"
                                defaultValue={recordForEdit.payrollCurrenty}
                                {...register('payrollCurrenty', {required: true})}
                            />
                            <Typography sx={{color:'red'}}>{errors.payrollCurrenty?.type === "required" && "Payroll currency is required"}</Typography>
                        </Box>
                        <Box flex={2}>
                            <TextField
                                id="Salary"
                                label="Salary *"
                                name="Salary"
                                {...register('Salary', {required: true})}
                            />
                            <Typography sx={{color:'red'}}>{errors.Salary?.type === "required" && "Salary is required"}</Typography>
                        </Box>
                    </Stack>
                    <br />
                    <Stack direction="row" spacing={2}>
                        <Box flex={2}>
                            Is person with disability
                            <Switch {...label}
                                {...register('ispersonwithDisability')}
                            />
                        </Box>
                    </Stack>
                    <Stack direction="row" spacing={2}>
                        <Box flex={2}>
                            Overtime applicable
                            <Switch {...label}
                                {...register('overtimeapplicable')}
                            />
                        </Box>
                    </Stack>
                    <Stack direction="row" spacing={2}>
                        <Box flex={2}>
                            <Gender register={register} errors={errors} />
                        </Box>
                    </Stack>

                    <br />
                    <Stack direction="row" spacing={2}>
                        <Box flex={2}>
                            <MaritalStatus register={register} errors={errors} />
                        </Box>
                    </Stack>

                    <br />

                    <Button variant="contained"
                        onClick={handleSubmit(handleRegistration, handleError)}
                    >Save</Button><Button onClick={handleCancel}>Cancel</Button>
                </form>
                <Notification
                    notify={notify}
                    setNotify={setNotify}
                />
            </Box>
        </Container>
    )
}
export default EditProfile