import { useState, useEffect } from "react";
import axios from 'axios';
import {
    FormControl,
    Select,
    InputLabel,
    MenuItem,
    Typography
} from "@mui/material"

function Positions(props) {
    const [position, setPosition] = useState([]);
    const {defaultValue} = props
    console.log(defaultValue)

    const getPositions = async () => {
        try {
            const data = await axios.get("https://192.168.7.109/api/Position");
            setPosition(data.data)
        }
        catch (e) {
            console.log(e);
        }
    }

    useEffect(() => {
        getPositions();
    }, []);

    const positionsElements = position.map(pos => {
        return <MenuItem value={pos.id}>Position {pos.id}</MenuItem>
    })

    return (
        <FormControl sx={{ width: '170px' }}>
            <InputLabel id="possitionId-label">Position</InputLabel>
            <Select
                labelId="demo-simple-select-label"
                id="possitionId"
                label="Position"
                sx={{ width: '238px' }}
                defaultValue={defaultValue}
                {...props.register('possitionId', {required: true})}
            >
                {positionsElements}
            </Select>
            <Typography sx={{color:'red'}}>{props.errors.possitionId?.type === "required" && "Position is missing"}</Typography>
        </FormControl>
    )
}
export default Positions