import { useState, useEffect } from "react";
import axios from 'axios';
import {
    FormControl,
    Select,
    InputLabel,
    MenuItem,
    Typography
} from "@mui/material"

function OfficeLocations(props) {
    const [officeLoction, setofficeLoction] = useState([]);
    const {defaultValue} = props;
    // alert(defaultValue)
    const getOfficeLocations = async () => {
        try {
            const data = await axios.get("https://192.168.7.109/api/Building");
            setofficeLoction(data.data)
        }
        catch (e) {
            console.log(e);
        }
    }

    useEffect(() => {
        getOfficeLocations();
    }, []);

    const OfficeLocationsElements = officeLoction.map(ol => {
        return <MenuItem value={ol.id}>{ol.buildingName}</MenuItem>
    })

    return (
        <FormControl sx={{ width: '170px' }}>
            <InputLabel id="officeLocation-label">Office location</InputLabel>
            <Select
                labelId="officeLocation-label"
                id="officeLocation"
                label="Office location"
                sx={{ width: '238px' }}
                defaultValue={defaultValue}
                {...props.register('officeLocation', {required: true})}
            >
                {OfficeLocationsElements}
            </Select>
            <Typography sx={{color:'red'}}>{props.errors.officeLocation?.type === "required" && "Office location is missing"}</Typography>
        </FormControl>
    )
}
export default OfficeLocations