import { useState, useEffect } from 'react';
import {
    Button,
    Switch,
    InputLabel,
    MenuItem,
    FormControl,
    Select,
    TextField,
    autocompleteClasses,
    Box,
    Container,
    Stack,
    Typography
} from "@mui/material"
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormLabel from '@mui/material/FormLabel';

export default function Nationality(props) {
    return (
        <FormControl>
            <FormLabel id="demo-row-radio-buttons-group-label">Gender *</FormLabel>
            <RadioGroup
                row
                aria-labelledby="demo-row-radio-buttons-group-label"
                name="row-radio-buttons-group"
                {...props.register('gender')}
            >
                <FormControlLabel value="female" {...props.register('gender', {required: true})} control={<Radio />} label="Female" />
                <FormControlLabel value="male" {...props.register('gender',  {required: true})} control={<Radio />} label="Male" />
            </RadioGroup>
            <Typography sx={{color:'red'}}>{props.errors.gender?.type === "required" && "Gender is required"}</Typography>
        </FormControl>
    )
}