import { useState, useEffect } from 'react';
import {
    Button,
    Switch,
    InputLabel,
    MenuItem,
    FormControl,
    Select,
    TextField,
    autocompleteClasses,
    Box,
    Container,
    Stack,
    Typography
} from "@mui/material"
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormLabel from '@mui/material/FormLabel';

export default function Nationality(props) {
    return (
        <FormControl>
            <FormLabel id="demo-row-radio-buttons-group-label">Marital Status *</FormLabel>
            <RadioGroup
                row
                aria-labelledby="demo-row-radio-buttons-group-label"
                name="row-radio-buttons-group"
                {...props.register('marialStatus')}
            >
                <FormControlLabel value="Single" {...props.register('marialStatus', {required: true})} control={<Radio />} label="Single" />
                <FormControlLabel value="Married" {...props.register('marialStatus', {required: true})} control={<Radio />} label="Married" />
                <FormControlLabel value="Divorced" {...props.register('marialStatus', {required: true})} control={<Radio />} label="Divorced" />
            </RadioGroup>
            <Typography sx={{color:'red'}}>{props.errors.marialStatus?.type === "required" && "Marital status is required"}</Typography>
        </FormControl>
    )
}