import * as React from 'react';
import { Helmet } from 'react-helmet-async';
import { useTheme } from '@mui/material/styles';
import { Component, forwardRef, useState, useEffect } from "react";
import Box from '@mui/material/Box';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableFooter from '@mui/material/TableFooter';
import TablePagination from '@mui/material/TablePagination';
import TableRow from '@mui/material/TableRow';
import TableHead from '@mui/material/TableHead';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/ModeEditOutlineOutlined';
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';
import Paper from '@mui/material/Paper';
import IconButton from '@mui/material/IconButton';
import FirstPageIcon from '@mui/icons-material/FirstPage';
import KeyboardArrowLeft from '@mui/icons-material/KeyboardArrowLeft';
import KeyboardArrowRight from '@mui/icons-material/KeyboardArrowRight';
import LastPageIcon from '@mui/icons-material/LastPage';
import Button from '@mui/material/Button'
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import {
   
    Stack,
    TextField,
    Toolbar
  } from '@mui/material';
import { variables } from "../../variables";
import Popup from '../../components/Popup';
import CustomizedDialogs from '../../components/Dialog/CustomizedDialogs'

import EditProfile from './EditProfile';

import Notification from '../../components/Notifications';
import HireForm from './HireForm';



interface TablePaginationActionsProps {
    count: number;
    page: number;
    rowsPerPage: number;
    onPageChange: (
        event: React.MouseEvent<HTMLButtonElement>,
        newPage: number,
    ) => void;
}

function TablePaginationActions(props: TablePaginationActionsProps) {
    const theme = useTheme();
    const { count, page, rowsPerPage, onPageChange } = props;

    const handleFirstPageButtonClick = (
        event: React.MouseEvent<HTMLButtonElement>,
    ) => {
        onPageChange(event, 0);
    };

    const handleBackButtonClick = (event: React.MouseEvent<HTMLButtonElement>) => {
        onPageChange(event, page - 1);
    };

    const handleNextButtonClick = (event: React.MouseEvent<HTMLButtonElement>) => {
        onPageChange(event, page + 1);
    };

    const handleLastPageButtonClick = (event: React.MouseEvent<HTMLButtonElement>) => {
        onPageChange(event, Math.max(0, Math.ceil(count / rowsPerPage) - 1));
    };

    return (
        <Box sx={{ flexShrink: 0, ml: 2.5 }}>
            <IconButton
                onClick={handleFirstPageButtonClick}
                disabled={page === 0}
                aria-label="first page"
            >
                {theme.direction === 'rtl' ? <LastPageIcon /> : <FirstPageIcon />}
            </IconButton>
            <IconButton
                onClick={handleBackButtonClick}
                disabled={page === 0}
                aria-label="previous page"
            >
                {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
            </IconButton>
            <IconButton
                onClick={handleNextButtonClick}
                disabled={page >= Math.ceil(count / rowsPerPage) - 1}
                aria-label="next page"
            >
                {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
            </IconButton>
            <IconButton
                onClick={handleLastPageButtonClick}
                disabled={page >= Math.ceil(count / rowsPerPage) - 1}
                aria-label="last page"
            >
                {theme.direction === 'rtl' ? <FirstPageIcon /> : <LastPageIcon />}
            </IconButton>
        </Box>
    );
}

function createData(name: string, calories: number, fat: number) {
    return { name, calories, fat };
}

export default function CustomPaginationActionsTable() {
    const [page, setPage] = React.useState(0);
    const [rowsPerPage, setRowsPerPage] = React.useState(5);
    const [rows, setRows] = useState([]);
    const [recordForEdit, SetRecordForEdit] = useState({})
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
    const [openPopup, setOpenPopup] = useState(false)
    const [openPopup2, setOpenPopup2] = useState(false)
    const navigate = useNavigate();
    const [filterval, setFilterval]=useState('');
    // Reading data

    const getHires = async () => {
        try {
            const Endpoint = `FullProfile`
            const data = await axios.get(variables.API_URL + Endpoint);

            setRows(data.data)

        }
        catch (e) {
            console.log(e);
        }
    }

    // Displaying Data

    useEffect(() => {
        getHires();
    }, []);



    // Paginations
    const emptyRows =
        page > 0 ? Math.max(0, (1 + page) * rowsPerPage - rows.length) : 0;

    const handleChangePage = (
        event: React.MouseEvent<HTMLButtonElement> | null,
        newPage: number,
    ) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (
        event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>,
    ) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        setPage(0);
    };

    // Open in Popup Modal

    const openInPopup = row => {
        SetRecordForEdit(row)
        setOpenPopup(true)
        // console.log(recordForEdit)
    }

    // POST
    const postData = (value) => {
        const Endpoint = `Profile`
        axios.post(variables.API_URL + Endpoint, value)
            .then((response) => {
                setNotify({
                    isOpen: true,
                    message: 'Employee profile added',
                    type: 'success'
                }
                )
                setOpenPopup(false);
            })
            .catch((e) => {
                console.log("Hello errors")
                setNotify({
                    isOpen: true,
                    message: 'Employee profile save failed',
                    type: 'error'
                }
                )
                setOpenPopup(false);
            })
    }

    // Updating Database
    const updatedata = (values) => {
        // console.log(values)
        const Endpoint = `employee`
        axios.put(variables.API_URL + Endpoint, { employeeID: values.employeeID, fullName: values.fullName, hireDate: values.hireDate, possitionId: values.possitionId, isManager: values.isManager, officelocation: values.officelocation })
            .then((response) => {
                setNotify({
                    isOpen: true,
                    message: 'Updated Successfully',
                    type: 'success'
                }
                )
                setOpenPopup(false);
            })
            .catch((e) => {
                console.log("Hello")
            })
    }

    // Deleting Data

    const DeleteRow = (row) => {

        const Endpoint = `employee/${row.employeeID}`

        axios.delete(variables.API_URL + Endpoint)
            .then(res => {
                const dataDelete = [...rows];
                const index = row.tableData.id;
                dataDelete.splice(index, 1);
                setRows([...dataDelete]);
                setNotify({
                    isOpen: true,
                    message: 'Sucessfully Deleted',
                    type: 'error'
                }
                )
            })
            .catch(error => {

                console.log("Error")

            })
    }
   

    const handleFilter=(e)=>{
        if(e.target.value==='')
        {
            getHires();
        }
        else
        {
            
            const filterResult= rows.filter(item=>item.fullname.toLowerCase().includes(e.target.value.toLowerCase()));
           setRows(filterResult)
        }
        setFilterval(e.target.value)
        }
    return (
        <>
            <Helmet>
                <title> HR - Profiles</title>
            </Helmet>
            
            <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
               
                <Box sx={{ display: 'flex', alignItems: 'flex-end' }}>
      
        <TextField  id="standard-basic" label="Search Employee" variant="standard" placeholder='Search'  value={filterval} onInput={(e)=>handleFilter(e)} />
        
      </Box>
        </Stack>
        
            <TableContainer component={Paper}>
                <Table sx={{ minWidth: 500 }} aria-label="custom pagination table" options={{}}>
                    <TableHead>
                        <TableRow >
                            <TableCell>View</TableCell>
                            <TableCell>Id</TableCell>
                            <TableCell >Name</TableCell>
                            <TableCell>Cell</TableCell>
                            <TableCell >Email</TableCell>
                            <TableCell>Calendar</TableCell>
                            <TableCell>Salary</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {(rowsPerPage > 0
                            ? rows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                            : rows
                        ).map((row) => (
                            <TableRow key={row.employeeid}>
                                <TableCell>
                                <Button variant='filled' onClick={() => navigate({
                                    pathname:`/dashboard/employees/profile/${row.employeeid}`
                                })} startIcon={<RemoveRedEyeIcon />} />
                                </TableCell>
                                <TableCell component="th" scope="row">
                                    {row.employeeid}
                                </TableCell>
                                <TableCell>
                                    {row.fullname}
                                </TableCell>
                                <TableCell >
                                    {row.cellphone}
                                </TableCell>
                                <TableCell >
                                    {row.email}
                                </TableCell>
                                <TableCell >
                                    {row.calendar}
                                </TableCell>
                                <TableCell >
                                    {row.salary}
                                </TableCell>
                            </TableRow>
                        ))}
                        {emptyRows > 0 && (
                            <TableRow style={{ height: 53 * emptyRows }}>
                                <TableCell colSpan={6} />
                            </TableRow>
                        )}
                    </TableBody>
                    <TableFooter>
                        <TableRow>
                            <TablePagination
                                rowsPerPageOptions={[5, 10, 25, { label: 'All', value: -1 }]}
                                colSpan={3}
                                count={rows.length}
                                rowsPerPage={rowsPerPage}
                                page={page}
                                SelectProps={{
                                    inputProps: {
                                        'aria-label': 'rows per page',
                                    },
                                    native: true,
                                }}
                                onPageChange={handleChangePage}
                                onRowsPerPageChange={handleChangeRowsPerPage}
                                ActionsComponent={TablePaginationActions}
                            />
                        </TableRow>
                    </TableFooter>
                </Table>
            </TableContainer>

            <Popup
                openPopup={openPopup}
                setOpenPopup={setOpenPopup}
                title={"Set Profile"}
            >
                <EditProfile
                    recordForEdit={recordForEdit}
                    inserdata={postData}
                    setOpenPopup={setOpenPopup}
                />
            </Popup>

            <Popup
                openPopup={openPopup2}
                setOpenPopup={setOpenPopup2}
                title="New Hire"
            >
                <HireForm setOpenPopup2={setOpenPopup2} />
            </Popup>

            <Notification
                notify={notify}
                setNotify={setNotify}
            />
        </>
    );
}