import {
    Button,
    Switch,
    InputLabel,
    MenuItem,
    FormControl,
    Select,
    TextField,
    autocompleteClasses,
    Box,
    Container,
    Stack,
    Typography
} from "@mui/material"
import axios from 'axios';
import { useState } from "react"
import { useForm } from "react-hook-form";
import { color } from "@mui/system";
import Position from "./Positions"
import OfficeLocation from "./OfficeLocation"
import Notification from '../../components/Notifications';


function HireForm(props) {
    const {insernewdata,setOpenPopup2} = props
    const { register, handleSubmit, formState: { errors } } = useForm({
        mode: "onBlur"
    });
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })

    const registerOptions = {
        fullName: { required: "Name is required" },
        hireDate: { required: "Hire date is required" },
        password: {
            required: "Password is required",
            minLength: {
                value: 8,
                message: "Password must have at least 8 characters"
            }
        }
    };
    const handleCancel = () => {
        return setOpenPopup2(false)
    }

    function handleRegistration(values) {
        insernewdata(values)
    }
   
    function handleError(event) { }
    const label = { inputProps: { 'aria-label': 'Switch demo' } };
    return (
        <Container maxWidth="sm" sx={{ margin: 0 }}>
            <Box
                sx={{ padding: '10px', borderRadius: '10px' }}
            >
                <form onSubmit={handleSubmit(handleRegistration, handleError)}>
                    <Stack direction="row" spacing={2}>
                        <Box flex={2}>
                            <TextField
                                id="fullName"
                                sx={{ width: '238px' }}
                                label="Full name"
                                name="fullName"
                                {...register('fullName', registerOptions.fullName)}


                            />
                            <Typography sx={{color:'red'}}>{errors.fullName?.type === "required" && "Name is required"}</Typography>
                        </Box>
                        <Box flex={4}>
                            <TextField
                                required
                                id="date"
                                name="date"
                                label="Hire date"
                                sx={{ width: '238px' }}
                                type="date"
                                defaultValue={Date.now()}
                                sx={{ width: 220 }}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                {...register('hireDate', registerOptions.hireDate)}
                            />
                            <Typography sx={{color:'red'}}>{errors.hireDate?.type === "required" && "Hiredate is required"}</Typography>
                        </Box>
                    </Stack>
                    <br />
                    <Stack direction="row" spacing={2}>
                        <Box flex={2}>
                            <Position register={register} errors={errors} />
                        </Box>
                        <Box flex={2} >
                            <OfficeLocation register={register} errors={errors} />
                        </Box>
                    </Stack>
                    <br />
                    <Stack direction="row" spacing={2}>
                        <Box flex={2}>
                            Is manager
                            <Switch {...label}
                                {...register('isManager')}
                            />
                        </Box>

                    </Stack>

                    <br />

                    <Button variant="contained"
                        onClick={handleSubmit(handleRegistration, handleError)}
                    >Save</Button>
                    <Button onClick={handleCancel}>Cancel</Button>
                </form>
                <Notification
                    notify={notify}
                    setNotify={setNotify}
                />
            </Box>
        </Container>
    )
}
export default HireForm
