import { useState, useEffect } from 'react';
import {
    Button,
    Switch,
    InputLabel,
    MenuItem,
    FormControl,
    Select,
    TextField,
    autocompleteClasses,
    Box,
    Container,
    Stack,
    Typography
} from "@mui/material"

export default function Calendar(props) {
    return (
        <FormControl fullWidth>
            <InputLabel id="Calendar-label">Calendar</InputLabel>
            <Select
                required
                labelId="Calendar-label"
                id="Calendar"
                label="Calendar"
                {...props.register('Calendar', {required: true})}
            >
                <MenuItem value={"Somaliland"}>Somaliland</MenuItem>
                <MenuItem value={"Somalia"}>Somalia</MenuItem>
                <MenuItem value={"DJibouti"}>DJibouti</MenuItem>
                <MenuItem value={"Ethipia"}>Ethipia</MenuItem>
                <MenuItem value={"Kenya"}>Kenya</MenuItem>
                <MenuItem value={"UK"}>UK</MenuItem>
                <MenuItem value={"USA"}>USA</MenuItem>
            </Select>
            <Typography sx={{color:'red'}}>{props.errors.Calendar?.type === "required" && "Calendar is required"}</Typography>
        </FormControl>
    )
}