import { useState, useEffect } from 'react';
import {
    Button,
    Switch,
    InputLabel,
    MenuItem,
    FormControl,
    Select,
    TextField,
    autocompleteClasses,
    Box,
    Container,
    Stack,
    Typography
} from "@mui/material"

export default function Nationality(props) {
    return (
        <FormControl fullWidth>
            <InputLabel id="Nationality-label">Nationality</InputLabel>
            <Select
                required
                labelId="Nationality-label"
                id="Nationality"
                label="Nationality"
                {...props.register('Nationality', {required: true})}
            >
                <MenuItem value={"Somaliland"}>Somaliland</MenuItem>
                <MenuItem value={"Somalia"}>Somalia</MenuItem>
                <MenuItem value={"DJibouti"}>DJibouti</MenuItem>
                <MenuItem value={"Ethipia"}>Ethipia</MenuItem>
                <MenuItem value={"Kenya"}>Kenya</MenuItem>
                <MenuItem value={"UK"}>UK</MenuItem>
                <MenuItem value={"USA"}>USA</MenuItem>
            </Select>
            <Typography sx={{color:'red'}}>{props.errors.Nationality?.type === "required" && "Nationality is required"}</Typography>
        </FormControl>
    )
}